.treemain {
  display: flex;
  width: 100%;
  justify-content: center;
}

.showtree_area {
  width: 35px;
  position: relative;
  display: flex;
}

.showtree_area:hover {
  animation-name: jogging;
  animation-duration: 1.2s;
  animation-iteration-count: infinite;
}

#chk_showtree {
  display: none;
}

.showtree_area::before {
  content: "Show/Hide Tree";
  color: lightslategray;
  
  font-size: 15px;
  position: absolute;
  top: -27%;
  left: 70%;
  width: 340%;
  transition-duration: 0.5s;
  opacity: 0;
}

.showtree_area:hover::before {
  opacity: 1;
}

#chk_showtree + label div {
  width: 1px;
  height: 1px;
  border-top: transparent 20px solid;
  border-right: gray 20px solid;
  border-left: transparent 0px solid;
  border-bottom: transparent 20px solid;
  transition-duration: 0.5s;
  transition-timing-function: linear;
  cursor: pointer;
  margin-right: 5px;
}

#chk_showtree:checked + label div {
  transform: rotateY(180deg);
}

.treearea {
  padding-left: 10px;
  display: block;
  width: 700px;
  overflow: scroll;
  height: 1000px;
  resize: horizontal;
  overflow: auto;
  white-space: nowrap;
  position: relative;
  transition-duration: 0.5s;
  transition-timing-function: linear;
}
.treeareahide {
  width: 0px;
}

.nodedetail {
  display: block;
  background-color: #e9e4d9;
  width: 100%;
  overflow: scroll;
  height: 1000px;
}

.treeele {
  display: flex;
  font-size: 30px;
  animation-duration: 0.5s;
  animation-name: slidein;
}

.tree_prefix,
.tree_prefix_showchild {
  background-color: #004f5f;
  width: 20px;
  height: 20px;
  cursor: pointer;
  margin-right: 5px;
}

.tree_prefix {
  clip-path: polygon(
    0 33%,
    33% 33%,
    33% 0,
    66% 0,
    66% 33%,
    100% 33%,
    100% 66%,
    66% 66%,
    65% 100%,
    33% 100%,
    33% 66%,
    0 66%
  );
}

.tree_prefix_showchild {
  clip-path: polygon(0 33%, 100% 33%, 100% 66%, 0 66%);
}

.treeele button {
  border: #004f5f 2px solid;
  color: #004f5f;
  background-color: white;
  border-radius: 5px;
  font-size: 15px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 3px;
  padding-bottom: 3px;
  cursor: pointer;
}

.treeele button:hover {
  background-color: #004f5f;
  color: white;
}

.treeele button + div {
  margin-left: 2px;
  width: 1px;
  width: 1px;
  border-top: transparent 15px solid;
  border-bottom: transparent 15px solid;
  border-right: #004f5f 15px solid;
  border-left: transparent 0px solid;
  animation-name: jogging;
  animation-duration: 1.2s;
  animation-iteration-count: infinite;
}

@keyframes jogging {
  0% {
    transform: translateX(0%);
  }
  50% {
    transform: translateX(20%);
  }
  100% {
    transform: translateX(0%);
  }
}

.searcondition_area {
  display: flex;
  justify-content: start;
  align-items: center;
  color: gray;
  font-size: 25px;
  font-weight: bold; 
  justify-content: start;
  align-items: center;
}

.addbtn {
  float: right;
  font-size: 20px;
  cursor: pointer;
}

.addbtn:hover {
  color: #ff4e00;
}

.delbtn {
  cursor: pointer;
  background-color: lightgray;
  color: #004f5f;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  cursor: "pointer";
  transition-duration: 0.5s;
  transition-timing-function: linear;
  border-radius: 10px;
}

.delbtn:hover {
  background-color: #004f5f;
  color: white;
}

.select_input {
  width: 95.5%;
  position: relative;
  z-index: 1;
}

.select {
  width: 100%;
  height: 30px;
  z-index: 2;
  background: transparent;
  position: relative;
  border-style: solid;
  border-color: lightgray;
  color: #004f5f;
  font-weight: bold;
  padding-left: 5px;
  -webkit-appearance: none; /*    */
  -moz-appearance: none; /*    */
  appearance: none; /*   */
}

.select option {
  background-color: #004f5f;
  color: white;
  font-size: 17px;
  font-weight: bold;
}

.select + div {
  width: 1px;
  height: 1px;
  position: absolute;
  border-top: gray 15px solid;
  border-bottom: transparent 3px solid;
  border-right: transparent 10px solid;
  border-left: transparent 10px solid;
  top: 5px;
  right: 5px;
  transition-duration: 0.3s;
  transition-timing-function: linear;
  z-index: 1;
}

.select:focus + div {
  transform: rotate(-180deg);
}

.loading-bg4 {
  display: flex;
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  background-color: rgba(100, 100, 100, 0.6);
  z-index: 999;
}

.loading-bg4 div {
  display: block;
  width: 10px;
  height: 10px;
  background-color: white;
  border-radius: 100%;
  margin-right: 10px;
  margin-left: 10px;
  animation-name: extension;
  animation-duration: 0.5s;
  animation-timing-function: cubic-bezier(0.39, 0.575, 0.565, 1);
  animation-iteration-count: infinite;
}
.loading-bg4 div:nth-child(1) {
  animation-delay: 0s;
}

.loading-bg4 div:nth-child(2) {
  animation-delay: 0.1s;
}

.loading-bg4 div:nth-child(3) {
  animation-delay: 0.2s;
}

.loading-bg4 div:nth-child(4) {
  animation-delay: 0.3s;
}
.loading-bg4 div:nth-child(5) {
  animation-delay: 0.4s;
}

@keyframes extension {
  0% {
    height: 10px;
    width: 10px;
    background-color: white;
  }
  50% {
    height: 30px;
    width: 30px;
    background-color: #004f5f;
  }
  100% {
    height: 10px;
    width: 10px;
    background-color: white;
  }
}

input[type="datetime-local"]::-webkit-calendar-picker-indicator {
  color: rgba(0, 0, 0, 0);
  opacity: 1;
  display: block;
  background: url(../images/calendar.svg);
  background-repeat: no-repeat;
  background-size: 22px 22px;
  background-position-x: right;
  border-width: thin;
  margin-right: 1px;
  cursor: pointer;
}


.msg-bg {
  display: flex;
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  background-color: rgba(100, 100, 100, 0.6);
  z-index: 999;
}

.msgarea {
  width: 500px;
  height: 200px;
  display: block;
  background-color: red;
}

#msgheader,#msgclose {
  background-color: #004f5f;
  color:white;
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 25px;
  text-transform: uppercase;
  font-weight: bold;
}

#msgbody{
  background-color: white;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;

}

#msgclose {
  cursor: pointer;
}
