html,
body {
  width: 100%;
  overflow-x: hidden;
}
body {
  justify-content: center;
  position: fixed;
  left: 0;
  top: 0;

  height: 100%;
  font-family: "Lato", "Serif";
  font-weight: 300;
  padding: 0;
  margin: 0;
  font-size: 16px;
  line-height: 28px;
  color: #004f5f;
  background: #004f5f;
  overflow-x: hidden;
}
h1 {
  font-size: 3em;
  line-height: 1.2em;
  margin: 0 0 0.3em;
}
h2 {
  font-size: 1.8em;
  line-height: 1.4em;
  margin: 0 0 0.3em;
}
h3 {
  font-size: 1.4em;
  line-height: 1.4em;
}
h4 {
  font-size: 1.25em;
  line-height: 1.4em;
}
h5 {
  font-size: 1.1em;
  line-height: 1.4em;
}
h6 {
  font-size: 1em;
  line-height: 1.2em;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  text-decoration: none;
}
p {
  padding: 0 0 1em;
  margin: 0;
}
a {
  color: #004f5f;
  outline: 0;
  font-weight: bold;
  -webkit-transition: all 0.8s ease;
  transition: all 0.8s ease;
}
a:hover {
  text-decoration: none;
  color: #1f2222;
  -webkit-transition: all 0.8s ease;
  transition: all 0.8s ease;
}
.highlight {
  color: #004f5f;
}

.msg {
  color: white;
  text-align: left;
  border-radius: 10px;
  border-width: 1px;
  animation-duration: 0.5s;
  animation-name: slidein;
  font-weight: bold;
  font-size: 20px;
  margin-top: 4px;
  margin-bottom: 4px;
}

@keyframes slidein {
  from {
    margin-left: -60%;
    width: 20%;
  }

  to {
    margin-left: 0%;
    width: 100%;
  }
}

.searchcondtion-area {
  display: block;
  width: 100%;
  background-color: wheat;
}

.mainarea {
  display: block;
  width: 100%;
  background-color: #e4ebed;
  border-style: solid;
  border-width: 1px;
  border-color: #004f5f;
  border-radius: 5px;
}

.popup {
  display: flex;
  justify-content: center;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  padding-top: 5%;
  background-color: rgba(100, 100, 100, 0.1);
  overflow: scroll;
  height: 90%;
  z-index: 30;
}

.loader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  overflow: visible;
  background-color: rgba(110, 100, 100, 0.2);
}
.loader .ball {
  background: #ff4e00;
  border-radius: 5px;
  display: block;
  height: 10px;
  left: -10px;
  position: absolute;
  top: 5px;
  width: 10px;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-name: alongfromleft;
  animation-timing-function: ease-in-out;
  margin-top: 24%;
}
.loader .ball2 {
  background: lightskyblue;
  border-radius: 5px;
  display: block;
  height: 10px;
  left: -10px;
  position: absolute;
  top: 5px;
  width: 10px;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-name: alongfromight;
  animation-timing-function: ease-in-out;
  margin-top: 26.4%;
}
.loader ul {
  margin: 0;
  padding: 0;
  margin-top: 25%;
}
.loader li {
  background-color: #ff4e00;
  border-radius: 5px;
  display: inline-block;
  margin: 0;
  height: 10px;
  width: 30px;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-name: around;
  animation-timing-function: linear;
}
.loader li:nth-child(1) {
  animation-delay: 0s;
}
.loader li:nth-child(2) {
  background-color: lightgreen;
  animation-delay: 0.2s;
}
.loader li:nth-child(3) {
  background-color: #004f5f;
  animation-delay: 0.4s;
}
.loader li:nth-child(4) {
  background-color: lightpink;
  animation-delay: 0.6s;
}
.loader li:nth-child(5) {
  background-color: lightskyblue;
  animation-delay: 0.8s;
}

@keyframes around {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
  }
}

@keyframes alongfromleft {
  0% {
    left: 45%;
    top: 5px;
  }
  25% {
    left: 54%;
    top: 5px;
  }
  50% {
    left: 54%;
    top: 45px;
  }
  75% {
    left: 45%;
    top: 45px;
  }
  100% {
    left: 45%;
    top: 5px;
  }
}

@keyframes alongfromight {
  0% {
    left: 54%;
    top: 0px;
  }
  25% {
    left: 45%;
    top: 0px;
  }
  50% {
    left: 45%;
    top: -40px;
  }
  75% {
    left: 54%;
    top: -40px;
  }
  100% {
    left: 54%;
    top: 0px;
  }
}

