.refresh-icon {
  content: "";
  width: 65px;
  height: 50px;
  background: url(../images/refresh-static.svg) no-repeat center center;
  background-size: 40px 40px;
  border-width: 0;
  transition: 0.2s;
  transition-timing-function: linear;
  color: #004f5f;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  font-size: 15px;
}

.refresh-icon::before {
  content: "Refresh";
  background-color: transparent;
  width: 150%;
  position: absolute;
  margin-top: 10px;
  top: 20px;
  left: 110%;
