  }
  100% {
    left: 32%;
    background-color: #004f5f;
  }
}

nav .start-menu2,
a:nth-child(2):focus ~ .animation {
  animation: fromlogotomenu2;
  animation-duration: 0.7s;
  animation-timing-function: linear;
}

@keyframes fromlogotomenu2 {
  0% {
    left: 50.1%;
    background-color: #004f5f;
  }
  50% {
