  z-index: 999;
  transition: all 0.5s;
}

input,
textarea {
  width: 95%;
  height: 25px;
  /* border-color: #004f5f; */
  border: #004f5f solid 1px;
  padding-left: 5px;
}
textarea {
  height: 80px;
}
input:hover {
  border-color: #004f5f;
  border-style: solid;
  border-width: 2px;
}

input[type="checkbox"] {
  display: none;
}

input[id="menuicon"] + label {
  display: block;
  position: fixed;
  top: 10%;
  right: 0px;
  width: 50px;
  height: 45px;
  cursor: pointer;
  transition: all 0.5s;
  z-index: 30;
}
input[id="menuicon"] + label span {
  display: block;
  position: absolute;
  width: 70%;
  height: 3px;
  border-radius: 30px;
  background: #c4c9cf;
  transition: all 0.5s;
}

input[id="menuicon"]:hover + label span {
  background: #004f5f;
  animation-duration: 0.3s;
  height: 5px;
  /* animation-name: dynamic-arrow;
	animation-timing-function: linear;
	animation-iteration-count: infinite; */
}

input[id="menuicon"] + label span:nth-child(1) {
  top: 25%;
  left: 2%;
  transform: rotate(-45deg);
}
input[id="menuicon"] + label span:nth-child(2) {
  top: 75%;
  left: 2%;
  transform: translateY(-50%);
  transform: rotate(45deg);
}
input[id="menuicon"] + label span:nth-child(3) {
  top: 25%;
  left: 15%;
  transform: rotate(-45deg);
}
input[id="menuicon"] + label span:nth-child(4) {
  top: 75%;
  left: 15%;
  transform: translateY(-50%);
  transform: rotate(45deg);
}

input[id="menuicon"]:checked + label span:nth-child(1) {
  transform: rotate(45deg);
}
input[id="menuicon"]:checked + label span:nth-child(2) {
  transform: rotate(-45deg);
}
input[id="menuicon"]:checked + label span:nth-child(3) {
  transform: rotate(45deg);
}
input[id="menuicon"]:checked + label span:nth-child(4) {
  transform: rotate(-45deg);
}

input[id="menuicon"]:checked + label {
  right: 97px;
  transition: all 0.5s;
}

input[id="menuicon"]:checked + label + .slidebar {
  right: 0px;
}

