.loading-bg1 {
  display: flex;
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  background-color: rgba(100, 100, 100, 0.6);
  z-index: 999;
}
.loading-element {
  width: 80px;
  height: 80px;
  position: relative;
}

.loading-element span {
  position: absolute;
  width: 5%;
  height: 15%;
  bottom: 0%;
  animation-name: loadingaction;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-duration: 0.7s;
}
.loading-element span:nth-child(odd) {
  background-color: #004f5f;
}

.loading-element span:nth-child(even) {
  background-color: #ffffff;
}

.loading-element span:nth-child(1) {
  left: 5%;
}

.loading-element span:nth-child(2) {
  left: 15%;
  animation-delay: 0.1s;
}

.loading-element span:nth-child(3) {
  left: 25%;
  animation-delay: 0.15s;
}

.loading-element span:nth-child(4) {
  left: 35%;
  animation-delay: 0.2s;
}
.loading-element span:nth-child(5) {
  left: 45%;
  animation-delay: 0.25s;
}
.loading-element span:nth-child(6) {
  left: 55%;
  animation-delay: 0.3s;
}
.loading-element span:nth-child(7) {
  left: 65%;
  animation-delay: 0.35s;
}
.loading-element span:nth-child(8) {
  left: 75%;
  animation-delay: 0.4s;
}

.loading-element span:nth-child(9) {
  left: 85%;
  animation-delay: 0.45s;
}

.loading-element span:nth-child(10) {
  left: 95%;
  animation-delay: 0.5s;
}

@keyframes loadingaction {
  0% {
    height: 0px;
    bottom: 0px;
  }
  50% {
    height: 100%;
    bottom: 0px;
  }
  100% {
    height: 0px;
    bottom: 0px;
  }
}

.loading-bg2 {
  /* background: linear-gradient(0.5deg, #474bff 0%, #bc48ff 100%); */
  background-color: #777;
  opacity: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0px;
  top: 0px;
  z-index: 999;
}

.loadingmain-wrapper {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
}

.loading-ele {
  width: 40%;
  height: 40%;
  position: absolute;
  background-color: transparent;
  animation-name: testmove;
  animation-duration: 1s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.loading-ele[id="inneritem"] {
  animation-name: rotateaction1;
}
.loading-ele[id="outeritem"] {
  animation-name: rotateaction2;
}

@keyframes rotateaction1 {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotateaction2 {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(-180deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}
.loading-ele span {
  display: block;
  width: 5%;
  height: 30%;
  border-radius: 20px;
  position: absolute;
  animation-name: changeColor;
  animation-duration: 1s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.loading-ele span:nth-child(1) {
  background-color: lightcoral;
  top: 2%;
  left: 50%;
  transform: translateX(-50%);
}
.loading-ele span:nth-child(2) {
  background-color: lightgreen;
  top: 9%;
  left: 73%;
  transform: rotate(45deg);
}
.loading-ele span:nth-child(3) {
  background-color: lightskyblue;
  top: 50%;
  left: 80%;
  transform: translateY(-50%) rotate(90deg);
}
.loading-ele span:nth-child(4) {
  background-color: lightslategray;
  bottom: 9%;
  left: 73%;
  transform: translateX(-50%) rotate(135deg);
}
.loading-ele span:nth-child(5) {
  background-color: lightseagreen;
  bottom: 2%;
  left: 50%;
  transform: translateX(-50%);
}
.loading-ele span:nth-child(6) {
  background-color: lightsalmon;
  bottom: 9%;
  right: 73%;
  transform: rotate(45deg);
}
.loading-ele span:nth-child(7) {
  background-color: lightskyblue;
  top: 50%;
  right: 80%;
  transform: translateY(-50%) rotate(90deg);
}
.loading-ele span:nth-child(8) {
  background-color: lightyellow;
  top: 9%;
  right: 73%;
  transform: translateX(-50%) rotate(135deg);
}

@keyframes changeColor {
  0% {
  }
  50% {
    background-color: white;
  }
  100% {
  }
}

.loading-bg3 {
  /* background: linear-gradient(0.5deg, #474bff 0%, #bc48ff 100%); */
  background-color: #777;
  opacity: 50%;
  display: flex;
  justify-content: center;
